import { api, tags, urlUtils } from '@igblsln/store';
export const paymentSheetApi = api.injectEndpoints({
    overrideExisting: false,
    endpoints: (build) => ({
        listPaymentSheet: build.query({
            query: ({ sheetId }) => {
                return {
                    url: urlUtils('/payment/purchase/', `?sheet_id=${sheetId || ''}`),
                };
            },
            providesTags: (result = []) => [
                ...result.map(({ key: id }) => ({ type: tags.PROJECT_PURCHASE_INQUIRY, id })),
                { type: tags.PROJECT_PURCHASE_INQUIRY, id: 'LIST' },
            ],
        }),
        getErrorProne: build.query({
            query: () => 'error-prone',
        }),
    }),
});
export const { useListPaymentSheetQuery, useGetErrorProneQuery } = paymentSheetApi;
